import React from 'react'

import {MainPageUIProvider} from '@bpinc/ad-main-page-ui-context'
import {createPlatformSpecific} from '@bpinc/ad-platform-specific-web-impl'
import {ScrollStateProvider} from '@bpinc/ad-scroll-state-context'
import {ADShortcutsProvider as ShortcutsProvider} from '@bpinc/lib-accessible-shortcuts-context'
import {AuthorizationModule} from '@bpinc/lib-authorization-module'
import {ChannelStateProvider} from '@bpinc/lib-chat-ui'
import {createProviderWrapper} from '@bpinc/lib-helpers'
import {ActiveInteractionContextProvider} from '@bpinc/lib-interaction-context'

import {VoiceTranscriptApp} from './VoiceTranscriptApp'


const AVTProviders = createProviderWrapper([
    ActiveInteractionContextProvider,
    MainPageUIProvider,
    ChannelStateProvider,
    ScrollStateProvider,
    ShortcutsProvider,
])

export const FullscreenAVTApp: React.FC = () => {
    return (
        <AuthorizationModule platformSpecific={createPlatformSpecific('avt')} app="avt" appRemoteName="voiceTranscript">
            {() => (
                <AVTProviders>
                    <VoiceTranscriptApp />
                </AVTProviders>
            )}
        </AuthorizationModule>
    )
}

