import type {ReactElement, ReactFragment} from 'react'
import {useCallback} from 'react'
import produce from 'immer'
import {flow, property} from 'lodash-es'

import {useOnItemRemoved} from '@bpinc/ad-interaction-state-hooks'
import {useScrollStateProviderBuilder} from '@bpinc/ad-scroll-state-context-builder'
import type {FCWithChildren} from '@bpinc/lib-types-helpers'

import type {ScrollStateData} from './types'


const defaultScrollStateData: ScrollStateData = {
    interactions: {},
    chatCenter: {},
}

export const ScrollStateProvider: FCWithChildren<ReactElement | ReactFragment> = ({children}) => {
    const {Provider, scrollStateRef} = useScrollStateProviderBuilder<ScrollStateData>({
        children,
        defaultScrollStateData,
    })

    const resetStateForInteraction = useCallback(
        function resetStateForItemCallback(itemId: string) {
            scrollStateRef.current = produce(scrollStateRef.current, state => {
                delete state.interactions[itemId]
            })
        },
        [scrollStateRef],
    )

    useOnItemRemoved(flow(property('id'), resetStateForInteraction))

    return Provider
}
